import React, { useEffect ,useRef, useState, useCallback } from 'react'
import { useRecoilState } from 'recoil';
import Button from '@mui/material/Button';
import IconButton from '@mui/material/IconButton';
import ClearIcon from '@mui/icons-material/Clear';

import { NPYReader } from "../jsm/loaders/NPYReader.js";
import { BVHParser } from '../jsm/loaders/BVHParser.js';

import { fileSelectorState ,trajState, quaternionState, fileNamesState, BVHTextState } from '../state'
import { rotationToQuat } from '../utils'
import DragAndDrop from './DragAndDrop'
import { fontSize } from '@mui/system';

const FileSelector = (props) => {

    const [showFileSelector, setFileSelector] = useRecoilState(fileSelectorState )

    const [ trajectory , setTrajectory] = useRecoilState(trajState)
    const [ quaternion , setQuaternion] = useRecoilState(quaternionState)
    const [ BVHText , setBVHText] = useRecoilState(BVHTextState)

    const [ fileNames , setFileNames ] = useRecoilState(fileNamesState)


    const trajInput = useRef();
    const rotationInput = useRef();
    const BVHInput = useRef();

    const onReadTraj = (file)=>{
        let trajPoints = []
        const readerTrajecNPY = new FileReader();
        const fileName = file.name

        readerTrajecNPY.onload = function(event) {
            const n = new NPYReader();
            const raw = n.load(readerTrajecNPY.result);
            const array = Array.from(raw.data);
            const n_frame = raw.shape[0];

            if (raw.shape[1] % 3 !== 0){
                alert("Invalid Shape")
            }else {
                for (let i = 0; i < n_frame; i++) {
                    const points = array.splice(0,3)
                    trajPoints = [...trajPoints,points]
                }
            }
            setTrajectory(trajPoints)
            setFileNames((prevState)=> ({ ...prevState, trajectory: fileName }))
        }
        readerTrajecNPY.readAsArrayBuffer(file);
    }


    const onReadRotation=(file)=>{
        const fileName = file.name
        const readerRotationNPY = new FileReader();
        readerRotationNPY.onload = function(event) {
            const n = new NPYReader();
            const raw = n.load(readerRotationNPY.result);
            const array = Array.from(raw.data);

            const n_frame = raw.shape[0];
            const n_joint = raw.shape[1] /9;

            const quat = []
            
            if (raw.shape[1] % 9 !== 0){
                alert("Invalid Shape")
            }else {
                for (let i = 0; i < n_frame ; i++) {
                    for (let j = 0; j < n_joint; j++) {
                        const start = i* n_joint* 9 + j * 9  //   rotation matrix +    joint
                        const m = array.slice( start, start+ 9)
                        const q = rotationToQuat(m)
                        if ( i === 0 ){
                            quat[j] = [ q ]
                        }else{
                            quat[j].push(q)
                        }
                    }
                }
            }
            setQuaternion(quat)
            setFileNames((prevState)=> ({ ...prevState, rotation: fileName }))
        }
        readerRotationNPY.readAsArrayBuffer(file);
    }


    const onReadBVH = (file)=>{
        const fileName = file.name
        const readerBVH = new FileReader();
        readerBVH.onload = function(event) {
            setBVHText(event.target.result)
            setFileNames((prevState)=> ({ ...prevState, bvh: fileName }))
        }
        readerBVH.readAsText(file)
    }


    const onChangeBVH= (event) =>{
        onReadBVH(event.target.files[0])
    }

    const onChangeRotation= (event) =>{
        onReadRotation(event.target.files[0])
    }

    const onChangeTraj= (event) =>{
        onReadTraj(event.target.files[0])
    }



    return (
        <div>
        { showFileSelector && 
        
            <DragAndDrop onReadBVH={onReadBVH} onReadRotation={onReadRotation} onReadTraj={onReadTraj}>
            <div style={{ position:'absolute', left:0, top:0, width: '100%', height: '100%', backgroundColor: 'rgba(255,255,255,0.7)'}}>
            <div style={{position:'relative', border:'solid 1px gray', borderRadius:50, width: 400, height:400 , left:'50%', top:'20%', transform:'translate(-50%, 0%)' }}>
                <IconButton style={{ position:'absolute', right:20, top:10 }} onClick={() => setFileSelector(false)}>
                    <ClearIcon/>
                </IconButton>

                <div style={{ marginTop: 50 , fontSize:20, fontWeight: 'bold' }} >
                <div>Drag and Drop</div>
                <div>Or Browse Files</div>
                </div>

                <div style={{ marginTop: 40 }}>
                    <Button style={{ marginTop: 10}} variant="outlined" onClick={()=>trajInput.current.click()}>Upload Trajectory</Button>
                    <input type="file" accept=".npy" style={{ display: "none" }} ref={trajInput} onChange={onChangeTraj} />
                    <div style={{height: 20}}  >{fileNames.trajectory} </div>
                    


                    <Button style={{ marginTop: 10}}variant="outlined" onClick={()=>rotationInput.current.click()}>Upload Rotation</Button>
                    <input type="file" accept=".npy" style={{ display: "none" }} ref={rotationInput} onChange={onChangeRotation} />
                    <div style={{height: 20}}>{fileNames.rotation} </div>


                    <Button style={{ marginTop: 10}} variant="outlined" onClick={()=>BVHInput.current.click()}>Upload BVH</Button>
                    <input type="file" accept=".bvh" style={{ display: "none" }} ref={BVHInput} onChange={onChangeBVH} />
                    <div style={{height: 20}}>{fileNames.bvh} </div>

                </div>


            </div>
            <div style={{position:'relative', left:'50%', top:'20%', transform:'translate(-50%, 0%)'  }}>
                <div style={{marginTop:10}}>npy    <Highlight color={'blue'}>result, rotation, valid, rot </Highlight>     <Highlight color={'blue'}>rotation </Highlight> , </div>
                <div style={{marginTop:10}}><Highlight color={'red'}>input, traj, trajectory</Highlight>     <Highlight color={'red'}>trajectory </Highlight> .</div>
            </div>            
        </div>
        </DragAndDrop>
        }
        </div>
        
    )
}


const Highlight = (props) =>{
    const {color, children} = props
    return(
        <span style={{ fontWeight:'bold', color:color }}> {children}</span>
    ) 

}

export default FileSelector;