import React, { useEffect ,useRef, useState } from 'react'
import Button from '@mui/material/Button';
import { useRecoilState } from 'recoil';
import { useSetRecoilState } from 'recoil';

import AnimationViewer from '../components/AnimationViewer';
import FileSelector from '../components/FileSelector';
import PlayController from '../components/PlayController'
import ProgressBar from '../components/ProgressBar';
import { fileSelectorState , fileNamesState} from '../state';


const LocalTestPage = ()=>{
    const [ fileNames , setFileNames ] = useRecoilState(fileNamesState)

    const setFileSelector = useSetRecoilState(fileSelectorState);
    const animationRef = useRef({})
    animationRef.current.isPlay = true
    animationRef.current.reverse = false
    animationRef.current.totalFrame = 0


    return (
        <div style={{ backgroundColor : '#f8f8f8'}}>
            
            <div style={{ display:'flex',  alignItems:'center', marginLeft: 30, paddingTop:40}}>
            
                <Button variant="outlined" style={{ height:40,  zIndex: 0 }} onClick={()=>setFileSelector(true)} >Upload Files</Button>

                <div style={{ display:'flex'}}>
                        {fileNames.trajectory!=="" && <div style={{height: 20, marginLeft: 10 ,fontWeight:'bold', color:'red'}}> Current Trajectory File : {fileNames.trajectory} </div>}
                        {fileNames.rotation!=="" && <div style={{height: 20, marginLeft: 10, fontWeight:'bold', color:'blue'}}> Current Rotation File : {fileNames.rotation} </div>}
                        {fileNames.bvh!=="" && <div style={{height: 20, marginLeft: 10, fontWeight:'bold', color:'green'}}> Current BVH File : {fileNames.bvh} </div>}

                </div>
            </div>

            <PlayController  animationRef = {animationRef}/>
            <div style={{ paddingRight:30, paddingLeft: 30 }}>
                <ProgressBar animationRef = {animationRef}/>
                <AnimationViewer animationRef = {animationRef}/>
            </div>
            
           
            <FileSelector/>

        </div>

    )
}


export default LocalTestPage;

