import React, { useEffect ,useRef, useState } from 'react'
import { useSetRecoilState } from 'recoil';

import PlayArrowIcon from '@mui/icons-material/PlayArrow';
import PauseIcon from '@mui/icons-material/Pause';

import SkipNextIcon from '@mui/icons-material/SkipNext';
import SkipPreviousIcon from '@mui/icons-material/SkipPrevious';

import FastForwardIcon from '@mui/icons-material/FastForward';
import FastRewindIcon from '@mui/icons-material/FastRewind';
import { IconButton } from '@mui/material';


const BUTTONWIDTH = 30;
const IMGHEIHGT = 35;
const IMGWIDTH = 35;

const PADDING = 10


const PlayController = (props) => {

    const { animationRef, style } = props

    const onClickPlay = (event) =>{
        if(animationRef.current){
            animationRef.current.isPlay = true 
            animationRef.current.reverse = false
        }
    }
    
    const onClickPause = (event) =>{
        if(animationRef.current){
            animationRef.current.isPlay = false 
        }
    }

    const onClickReverse = (event) => {
        if(animationRef.current){
            animationRef.current.reverse = true
            animationRef.current.isPlay = true 
        }
    }

    return(
        <div style={{ display: 'flex', flexDirection: 'row', justifyContent: 'center', border: '0px solid black', borderRadius: 5,  }}>
            <IconButton onClick={onClickReverse} sx={{color:'black'}} >
                <PlayArrowIcon style={{ width: IMGWIDTH, height:IMGHEIHGT , transform: "rotate(180deg)"}}/>
            </IconButton>
            <IconButton onClick={ onClickPause} sx={{color:'black'}} >
                <PauseIcon style={{ width: IMGWIDTH, height:IMGHEIHGT }}/>
            </IconButton>
            <IconButton onClick={onClickPlay} sx={{color:'black'}} >
                <PlayArrowIcon style={{ width: IMGWIDTH, height:IMGHEIHGT }}/>
            </IconButton>
        </div>
    )
}

export default PlayController;