import React, { useEffect ,useRef, useState, useMemo } from 'react'
import * as THREE from 'three'
import { useRecoilState, useRecoilValue, useSetRecoilState } from 'recoil';
import { Canvas, useFrame, useThree } from '@react-three/fiber'
import { useAnimations } from "@react-three/drei";
import { trajState, quaternionState,BVHTextState, currFrameState, progressBarState } from '../state'
import * as dat from "dat.gui";
import { BVHParser } from '../jsm/loaders/BVHParser';

const AnimationClip = (props)=>{
    const { animationRef }   = props;
    const [ trajectory , setTrajectory] = useRecoilState(trajState)
    const [ quaternion , setQuaternion] = useRecoilState(quaternionState)
    const [ BVHText , setBVHText] = useRecoilState(BVHTextState)
    const setFrame = useSetRecoilState(currFrameState)
    
    const { scene, } = useThree();    

    const FPS = 60

    let mixer, skeletonHelper;


    if( BVHText.length >0  && trajectory.length !== 0 && quaternion.length !== 0 ){

        if( trajectory.length === quaternion[0].length){

            const parser = new BVHParser();
            const result = parser.parse( BVHText)
    
            skeletonHelper = new THREE.SkeletonHelper( result.skeleton.bones[ 0 ] );
            skeletonHelper.skeleton = result.skeleton; // allow animation mixer to bind to THREE.SkeletonHelper directly
    
            const boneContainer = new THREE.Group();
            boneContainer.add( result.skeleton.bones[ 0 ] );

            scene.children = scene.children.filter(child => child.type !== 'SkeletonHelper')
            scene.add( skeletonHelper );
            scene.add( boneContainer );
    
            // play animation
            mixer = new THREE.AnimationMixer( skeletonHelper );

            const n_frame = trajectory.length
            animationRef.current.totalFrame = n_frame
            const duration = n_frame / FPS
            result.clip.duration = duration 
            const new_time = new Array(n_frame).fill(0).map((_, index) => index * 1/FPS)
    
            result.clip.tracks = result.clip.tracks.map( (track, index) => {
                if( track.name.includes('.bones[Hips].position') ){
                    track.times = new_time
                    track.values = trajectory.flat()
                }else if(track.name.includes('position') ){
                    track.times = new_time
                    const std = track.values.slice(0,3)
                    track.values = new Array(n_frame).fill(0).map( _ => [...std]).flat()
                }else if( track.name.includes('quaternion') ){
                    track.times = new_time
                    track.values = quaternion[(index-1)/2].flat()
                }
                return ( track )

            })

            mixer.clipAction(result.clip).play()
        }else{
            alert(" trajectory   rotation   .")
        }
    }


    useFrame((state, delta) => { 
        if (mixer){
            if(animationRef.current.isPlay){
                const totalFrame = animationRef.current.totalFrame
                if(animationRef.current.reverse){
                    mixer.update( -delta )
                }else{
                    mixer.update( delta )
                    setFrame(parseInt(mixer.time*FPS)%totalFrame)
                }
            }
        }
    })


    return(
        <group ref={animationRef} >
            <UpdateFrameByProgressBar mixer={mixer} FPS={FPS} />
        
        </group>
    )

}

const UpdateFrameByProgressBar = (props)=>{
    const progressBar = useRecoilValue(progressBarState)
    const {mixer, FPS} = props
    useEffect(()=>{
        if (mixer){
            mixer.setTime( progressBar / FPS )
        }
        console.log(progressBar)
    },[progressBar])

    return(<group></group>)

}



export default AnimationClip;