import React from 'react'
import { atom } from 'recoil';

const fileSelectorState = atom({
    key: 'fileSelectorState', 
    default: false, 
})


const trajState = atom({
    key: 'trajState', 
    default: [], 
});


const quaternionState = atom({
    key: 'quaternionState', 
    default: [], 
});

const BVHTextState = atom({
    key: 'BVHTextState', 
    default: "", 
});

const fileNamesState = atom({
    key: 'fileNamesState', 
    default: { 
        trajectory: "",
        rotation: "",
        bvh: ""
    }, 
});

const currFrameState = atom({
    key: 'currFrameState', 
    default: 0, 
});


const progressBarState = atom({
    key: 'progressBarState', 
    default: 0, 
});


export { fileSelectorState ,trajState, quaternionState, fileNamesState,BVHTextState, currFrameState,progressBarState }