import React,{ useState, useEffect, useRef} from 'react'
import { Slider } from '@mui/material';
import { useRecoilState, useRecoilValue } from 'recoil';
import { currFrameState, progressBarState } from '../state';

const ProgressBar = (props) => {

    const { animationRef } =  props
    const sliderRef = useRef()
    const maxFrame = animationRef.current ? animationRef.current.totalFrame : 0

    const [ currFrame, setFrame ] =  useRecoilState( currFrameState );
    const [ progressBar, setProgressBar ] =  useRecoilState( progressBarState );

    const handleChange = ( event, newValue )=>{
        setProgressBar(newValue)
        setFrame(newValue)
    }


    return(
        <div style={{paddingLeft : 30, paddingRight: 30}}>

        <Slider
            ref = {sliderRef}
            sx={{ flexGrow: 1, }}
            defaultValue = { currFrame }
            size="small"
            aria-label="Small"
            min={0}
            max= {maxFrame-1}
            step={1}
            onChange={handleChange}
            value = {currFrame}
        />

        </div>


    )
}

export default ProgressBar;