import React, { useEffect ,useRef, useState, useCallback } from 'react'
import { useRecoilState, useRecoilValue } from 'recoil';
import * as THREE from 'three'
// import { MeshLine, MeshLineMaterial } from 'three.meshline';
import { trajState } from '../state'


const TrajectoryLine = () => {
    const trajectory = useRecoilValue(trajState)

    const [line, setLine] = useState( new THREE.Line())
    const [projectionLine, setProjectionLine] = useState( new THREE.Line())


    useEffect(()=>{
        const material = new THREE.LineBasicMaterial( { color: 0x999999 } );
        // const material = new MeshLineMaterial({color:  new THREE.Color( 'gray' ) , lineWidth: 20});
        const projectionMaterial = new THREE.LineBasicMaterial( { color: 0x999999 } );

        const n_frame = trajectory.length
        const points = []
        const passedPoints = []
        const projectionPoints = []
        const passedProjectionPoints = []
        for (let i = 0; i < n_frame; i++) {
            const [x, y, z] = trajectory[i]
            points.push( new THREE.Vector3( x, y, z ) );
            passedPoints.push( new THREE.Vector3( x, y, z ) );

            projectionPoints.push( new THREE.Vector3( x, 0, z ) );
            passedProjectionPoints.push( new THREE.Vector3( x, 0, z ) );

        }

        const geometry = new THREE.BufferGeometry().setFromPoints( points );
        const projectionGeometry = new THREE.BufferGeometry().setFromPoints( projectionPoints);

        // setLine(new THREE.Line( geometry, material ));
        // const trajectoryLine = new MeshLine();
        // const geometry = new THREE.BufferGeometry()
        // geometry.setAttribute("position", new THREE.BufferAttribute(trajectory));
        // trajectoryLine.setGeometry(geometry);
        // trajectoryLine.setPoints(points);

        // const mesh = new THREE.Mesh(trajectoryLine, material);
        // setLine(mesh);

        setLine(new THREE.Line( geometry, material ));

        setProjectionLine(new THREE.Line( projectionGeometry, projectionMaterial ));

    },[trajectory ])

    return (
        <group>
            <primitive key={'trajLine'} object={line} />
            <primitive key={'projLine'} object={projectionLine} />


        </group>

    )
}


export default TrajectoryLine;


