import React, { useEffect ,useRef, useState } from 'react'
import * as THREE from 'three'
import { Canvas , useThree } from '@react-three/fiber'
import { useRecoilBridgeAcrossReactRoots_UNSTABLE } from 'recoil';
import { OrbitControls } from "../jsm/controls/OrbitControls"
import AnimationClip from './AnimationClip';
import TrajectoryLine from './TrajectoryLine'

const CameraController = () => {
    const { camera, gl } = useThree();
    const focus = new THREE.Vector3(0,0,0)
    camera.lookAt(focus)
    useEffect(() => {
      const controls = new OrbitControls(camera, gl.domElement);
      controls.target = focus
      return () => {
        controls.dispose();
      };
    }, [camera, gl]);
    return null;
};

const ResizeCanvas =(props)=>{

    const { gl } = useThree();    

    useEffect(()=>{
        gl.setSize( window.innerWidth - 60, window.innerHeight - 200 );
        const handleResize = () => {               
            gl.setSize( window.innerWidth - 60, window.innerHeight - 200 );
        }
    
        window.addEventListener('resize', handleResize)
        return () =>{
            window.removeEventListener('resize', handleResize)
        }
    },[])
}


const AnimationViewer = (props) => {
    const { animationRef } = props
    const RecoilBridge = useRecoilBridgeAcrossReactRoots_UNSTABLE();
    const canvasRef = useRef({})
    
    const GRID_SIZE = 1000

    const axisHelper = new THREE.AxesHelper(GRID_SIZE)

    const geometry = new THREE.PlaneGeometry( GRID_SIZE, GRID_SIZE );
    const material = new THREE.MeshBasicMaterial( {color: 0xdcdcdc, side: THREE.DoubleSide} );
    const plane = new THREE.Mesh( geometry, material );
    plane.rotateX( - Math.PI / 2);

    return(
        <div >
            <Canvas 
            ref={canvasRef}
            camera={{ fov:60, aspect: window.innerWidth / window.innerHeight , far: 100000, position: [ 50, 50, -50] }} >
                <RecoilBridge>
                <ResizeCanvas/>
                <CameraController />
                <ambientLight intensity={1}/>
                <gridHelper args={[GRID_SIZE,50,0x444444,0x404040]} /> 
                <primitive object={plane} />
                <primitive object={axisHelper} />
                <AnimationClip animationRef={animationRef} />
                <TrajectoryLine/> 
                </RecoilBridge> 
            </Canvas>
        </div>
    )
}


export default AnimationViewer;
